<template>
	<div>
		<div @click="showSelect">{{value.val}}</div>
			<mt-popup
            v-model="selectTypeVisible"
            style="width:80%"
            pop-transtion="popop-fade"
            class="select-dict"
          >
            <mt-picker :slots="selectTypeSlots"   @change="selectTypeChange" :visible-item-count="3"></mt-picker>
            <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
              <button class="btn" @click="sureSelectType">确认</button>
              </div>
          </mt-popup>
	</div>
</template>
<script type="text/javascript">
import {Picker} from 'mint-ui'
Vue.component(Picker.name,Picker);
	export default{
		data(){
			return {
				selectTypeVisible:false,
        selectTypeSlots:[],
        year:"",
         month:"",
          date:"",
			}	
		},
		props:{
			 value:{
        type:Object,
         default:function() {
        return {
          val:"19700101"
        };
        }
     	 },
		},
		     created:function(){
		     	var dateStr=this.value.val;
		     	if(this.value=="19710101"){
		     		var dateStr=new Date().Format('yyyy-MM-dd').replace(/-/g,'')
		     	}
		     	var yearDefault=dateStr.substr(0,4);
		     	var monthDefault=dateStr.substr(4,2);
		     	var dateDefault=dateStr.substr(6,2);
		     	this.year=yearDefault;
		     	this.month=monthDefault;
		     	this.date=dateDefault;
		     	console.log("yearDefault=",yearDefault,"monthDefault=",monthDefault,"dateDefault=",dateDefault)
		     	var yearRow=[];
		     	for(let i=1970;i<2050;i++){
		     		yearRow.push(i+"");
		     	}
		     	var monthRow=[];
		     	for(let i=1;i<13;i++){
		     		if(i<10){
		     			monthRow.push("0"+i+"");
		     		}else{
		     			monthRow.push(i+"");
		     		}
		     		
		     	}
		     	var dateRow=[];
		     	for(let i=1;i<32;i++){
		     		if(i<10){
		     			dateRow.push("0"+i+"");
		     		}else{
		     			dateRow.push(i+"");
		     		}
		     		
		     	}
      		this.selectTypeSlots=[
        {
        flex:1,
        value:yearDefault,
        values:yearRow
        },
        {
          divider:true,
          content:"-",
        },
        {
        flex:1,
        value:monthDefault,
        values:monthRow
        },
        {
          divider:true,
          content:"-",
        },
        {
        flex:1,
        value:dateDefault,
        values:dateRow
        }

        ]

      		// this.$nextTick(()=>{
      		// 	setTimeout(()=>{
      		// 		this.selectTypeSlots[0].values=["2017","2016"];
      		// 		this.selectTypeSlots[1].values=["01","02"];
      		// 		this.selectTypeSlots[2]={
        // flex:1,
        // value:"03",
        // values:["01","02","03"]
        // }
      		// 	},100)
      		// })
      },
		methods:{
			 showSelect:function(){
        this.selectTypeVisible=true;
      },
 
      selectTypeChange:function(picker,value){
       // if(value[0]==undefined){
       // 	 picker.setSlotValue(0,this.selectTypeSlots[0].values[0])
       // }
       // if(value[1]==undefined){
       // 	 picker.setSlotValue(1,this.selectTypeSlots[1].values[0])
       // }
       //  if(value[2]==undefined){
       // 	 picker.setSlotValue(2,this.selectTypeSlots[2].values[0])
       // }
       console.log(value)
       if(value[0]!==undefined){
       	this.year=value[0];
       }
       if(value[1]!==undefined){
       	this.month=value[1];
       }
       if(value[2]!==undefined){
       	this.date=value[2];
       }
      

      },
      dissSelectType:function(){
          this.selectTypeVisible=false;
      },
      sureSelectType:function(){
          this.selectTypeVisible=false;
          this.value.val=this.year+this.month+this.date;
          this.$emit("callback",this.value.val)
      },
		}
	}
</script>
<style type="text/css" scoped>
    .select-dict{
      width: 80%;
    z-index: 2011;
    box-shadow: 3px 3px 12px #666;
    padding: 12px;
      }
</style>



