<template>
		<div class=" _full_router contain-white" style="background:#eee;">
    <custHeader :title="title"></custHeader>
          <div class="query-date" style="background:#fff;">
              <div class="normal-box">
                  <div >
                      <div class="query-date-t">起始日期</div>
                        <dateSelect @callback="freshen" v-model="startDate" class="query-date-time"></dateSelect>
                    </div>
					<div><i class="fa fa-exchange" style="    font-size: 22px;color: #E40202;margin-top: 12px;"></i></div>
                   <div>
                        <div class="query-date-t">截止日期</div>
                        <dateSelect @callback="freshen" v-model="endDate" class="query-date-time"></dateSelect>
                   </div>
              </div>
          </div>

    			 <ul style="background:#fff;margin-top:12px;" class="my-hold-container list-auto"
				 	v-infinite-scroll="loadMore"
					infinite-scroll-disabled="loading"
					infinite-scroll-distance="10"
					infinite-scroll-immediate-check="false"
				 >
				 <li>
                <div style="width:36vw;">委托时间</div>
                <div style="width:19vw;" class="text-right">委托/均价</div>
                <div style="width:20vw;" class="text-right">委托/数量</div>
                <div class="text-right">业务/状态</div>
                </li>
                <li v-for="(item,index) in qryList" :class="styleColorByBiz(item.stkBiz)">
                   <div style="width:36vw;"><div>{{item.stkName}}</div><div style="font-size:12px;">{{item.orderTime | datetimetext2}}</div></div>
                   <div style="width:19vw;" class="text-right"><div>{{item.orderPrice | toMoney}}</div><div >{{cjjj(item) | toMoney}}</div></div>
                   <div style="width:20vw;" class="text-right"><div>{{item.orderQty}}</div><div>{{item.matchedQty}}</div></div>
                   <div class="text-right"><div  v-remoteoptionsspan="dicts.stkBiz">{{item.stkBiz}}</div><div v-remoteoptionsspan="dicts.orderStatus" style="font-size:12px;">{{item.orderStatus}}</div></div>
                </li>

            </ul>
            <div v-show="qryList.length==0" class="jqqd" >
                当前没有委托订单
            </div>

             <!--  <mt-popup
            v-model="selectTypeVisible"
            style="width:80%"
            pop-transtion="popop-fade"
            class="select-dict"
          >
            <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="3"></mt-picker>
            <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
              <button class="btn" @click="sureSelectType">确认</button>
              </div>
          </mt-popup> -->
		</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox,InfiniteScroll,Toast} from 'mint-ui'
import custMixin from  'util/custMixin'
import custHeader from  'components/custHeader'
import dateSelect from  'components/dateSelect'

Vue.use(InfiniteScroll);
export default{
    data(){
       return {
       title:"历史委托",
       startDate:{val:""},
	   loading: false,
       endDate:{val:""},
                 qryList:[
                    ],
                 dicts:{
                      orderStatus:{
                        name:"ORDER_STATUS",
                      },
                      stkBiz:{
                        name:"STK_BIZ"
                      }
				  },
		pars:{
				// 	sort:"orderTime",
				// 	order:"desc",
	 			limit:global.pageSize,
	 			offset:0,
	 			stkex:global.scdm,
	 			bgnDate:"",
	 			 endDate:""
		 	}
            }
    },
    created:function(){
		var date = (new Date()).Format("yyyy-M-d");
		   this.startDate={val:date.replace(/-/g,"")};
		   this.endDate={val:date.replace(/-/g,"")};
      this.upCltOrderCancelQry();
    },
     components:{
    custHeader,
    dateSelect
  },
  mixins:[custMixin],
    methods:{
		cjjj:function(item){
			return item.matchedQty==0?0:item.matchedAmt.div(item.matchedQty);
		},
      freshen:function(){
            // var pars={
            //     sort:"orderTime",
            //     order:"desc",
            //     limit:100,
            //     offset:0,
            //     stkex:global.scdm,
            //     bgnDate:this.startDate.val,
            //     endDate:this.endDate.val,
            // };
            // axiosUtil.upCltOrderQryInfo(pars).then(data=>{
            //     var data=data.data;
            //     this.qryList=data.rows;
            // })

			this.pars.offset=0;
			this.upCltOrderCancelQry();
      },
        upCltOrderCancelQry:function(){
        //  var date = (new Date()).Format("yyyy-M-d");
			this.pars.bgnDate= this.startDate.val;
			this.pars.endDate= this.endDate.val;
            axiosUtil.upCltOrderQryInfo(this.pars).then(data=>{
                var data=data.data;
                this.qryList=data.rows;
            })
        },
		loadMore:function() {
	      let self = this;
	      this.loading = true;
	      this.pars.offset  +=global.pageSize;
	      axiosUtil.upCltOrderQryInfo(this.pars).then(data=> {
	        var data = data.data;

	        this.qryList = this.qryList.concat(data.rows);

	        if (data.total == this.qryList.length) {
	          this.loading = true;
	          Toast({
	            message: "已加载最后一条",
	            position: "bottom",
	            duration: 1000
	          });
		  }else{
			    this.loading = false;
		  }

	      })
	    }
    }
}
</script>
<style type="text/css" scoped>
  .app-content{
    background: #fff;
  }
  .my-hold-container li{
    border-bottom: 1px solid #efefef;
  }
  .my-hold-container{
  	padding-bottom: 90px;
  }

</style>



